.App {
  text-align: center;
  background-color: #ffffff;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

@font-face {
  font-family: 'Helvetica-Medium';
  font-style: normal;
  font-weight: normal;
  src: url('./assets/fonts/helvetica-webfont.woff'); /* IE9 Compat Modes */
  src: local('Open Sans'), url('./assets/fonts/helvetica-webfont.woff') format('woff'), /* Modern Browsers */
}

@font-face {
  font-family: 'Helvetica-Bold';
  font-style: normal;
  font-weight: bold;
  src: url('./assets/fonts/helvetica-bold.woff'); /* IE9 Compat Modes */
  src: local('Open Sans'), url('./assets/fonts/helvetica-bold.woff') format('woff'), /* Modern Browsers */
}