.slick-slide  {
    height: 969px;

    img {
        margin: auto;
        width: 100%;
    }
}

.slick-arrow {
    position: absolute;
}
.slick-slider:hover{
    .slick-prev {
        -webkit-transition: all 0.25s ease-in-out;
        -moz-transition: all 0.25s ease-in-out;
        -o-transition: all 0.25s ease-in-out;
        transition: all 0.25s ease-in-out;
        opacity: 1;
}

    .slick-next {
        -webkit-transition: all 0.25s ease-in-out;
        -moz-transition: all 0.25s ease-in-out;
        -o-transition: all 0.25s ease-in-out;
        transition: all 0.25s ease-in-out;
        opacity: 1;
    }
}

.slick-prev {
    -webkit-transition: all 0.25s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    width: 15px;
    height: auto;
    left: 2vw;
    opacity: 0.1;
    z-index: 2;
}

.slick-next {
    -webkit-transition: all 0.25s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    width: 15px;
    height: auto;
    right: 2vw;
    opacity: 0.1;
}

.slick-dots {
    bottom: 30px;

    li {
        border-bottom: none;
    }
}