.tw-client-photo {
    width: 90%;
    height: auto;
    margin: 0 auto;
    padding-top: 10vh;

    .photo-container {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
        grid-auto-rows: 200px;
        grid-gap: 5px;
        grid-auto-flow: dense;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .vertical {
            grid-row: span 2;
            grid-column: span 3;
        }
        .horizontal{
            grid-column: span 1;
            grid-row: span 2;
        }

        .big{
            grid-column: span 2;
            grid-row: span 2;
        }
    }
}

