.tw-gallery {
    width: 75%;
    height: auto;
    margin: 0 auto;
    padding-top: 10vh;
    background-color: #ffffff;
    
    .tw-gallery-title {
        width: 100%;
        text-align: left;
        padding: 7vh 0;
        img {
            width: 125px;
            height: auto;
        }
    }

    .container-reverse {
        flex-flow: row-reverse !important;
    }
    
    .tw-gallery-container {
        position: relative;
        height: 65vh;
        display: flex;
        flex-flow: row;
        justify-content: center;

        .tw-gallery-info {
            width: 70%;
            color: #000000;
            position: relative;
            background-color: #ffffff;
            
            .gallery-info {
                // position: absolute;
                font-size: 2rem;
                padding: 3vh 4vw;
                // margin: 0;
                // top: 50%;
                // left: 50%;
                // margin-right: -50%;
                // transform: translate(-50%, -50%);

                p {
                    margin: 0;
                    &.date{
                        font-size: 1.2rem;
                    }
                    
                    &.client {
                        font-family: Helvetica-Bold;
                        font-size: 2.4rem;
                        margin-bottom: 10px;
                    }

                    &.location {
                        font-family: Helvetica-Medium;
                        font-size: 1.4rem;
                    }
                }
            }
        }
        .left-gallery-info {
            .gallery-info {
                text-align: right;
                border-left: 1px solid #000000;
                min-height: 25vh;
            }
        }
        
        .right-gallery-info {
            .gallery-info {
                text-align: left;
                border-right: 1px solid #000000;
                min-height: 25vh;
            }
        }

        .tw-gallery-thumb {
            // background-image: url('../../../assets/carousel/slide-1.jpg');
            // background-position: center !important;
            // background-repeat: no-repeat !important;
            // background-size: cover !important;
            width: 100%;
            .gallery-thumb-image {
                background-repeat: no-repeat !important;
                background-size: 42vw;
                width: 800px;
                height: 500px;
                // box-shadow: 5px 5px 2px 1px rgba(0, 0, 0, 0.3);
                cursor: pointer;

                .gallery-thumb-shadow {
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0,0,0,0.4);
                    opacity: 0;
                    transition-duration: 0.5s;
                    img {
                        width: 50px;
                        height: auto;
                        position: relative;
                        margin: 0 auto;
                        top: 50%;
                        margin-top: -50%;
                    }
                }

                &:hover {
                    .gallery-thumb-shadow {
                        transition-duration: 0.5s;
                        opacity: 1;
                    }
                }
            }
        }

        .right-gallery-thumb {
            .gallery-thumb-image {
                float: right;
                // background-position: top right !important;
            }
        }

        .left-gallery-thumb {
            .gallery-thumb-image {
                float: left;
                // background-position: top left !important;
            }
        }
    }
}