.tw-footer {
    width: 100%;
    min-height: 25vh;
    // padding-top: 2vh;
    background-color: #ffffff;
    box-shadow: 0 -1px 5px rgba(190,190,190,.4);
    // background-color: #f3ebe6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 5px;
    
    .tw-footer-line {
        height: auto;
        hr {
            display: block;
            width: 99%;
            margin: 0 auto;
            border: none;
            background-color: rgba(33,33,33,0.12);
        }

    }
    .tw-footer-children {
        width: 100%;
        height: auto;
    }

    .child-1 {
        padding-top: 3vh;

        img{
            width: 93px;
        }
    }
    
    .child-2 {
        padding: 2vh 0 0 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        
        .tw-sosial-text {
            width: 100%;
        }

        .tw-sosial-icons {
            width: 5vw;
            margin: 0 auto;
            display: flex;
            justify-content: space-around;
            img {
                width: 25px;
                height: auto;
            }
        }
    }

    .child-3 {
        padding: 0vh 0 0vh 0;

        p {
            font-family: 'Helvetica';
            font-size: 0.7rem;
            color: #000000a1;
        }
    }
}