.tw-nav-front {
    margin: auto;
    padding: 0;
    width: 100%;
    height: auto;
    position: fixed;
    background-image: linear-gradient(to bottom, rgba(77, 77, 77, 0.349), rgba(255, 255, 255, 0));
    z-index: 1;
}

.tw-nav {
    margin: auto;
    padding: 0;
    width: 100%;
    height: auto;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 1px 3px rgba(190,190,190,.4);
    // background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;

    .tw-main-nav  {
        width: 65%;
        height: 10vh;
        margin: 0px  auto;
        
        .tw-main-logo {
            width: 70px;
            height: auto;
        }

        li {
            // font-size: 1.11rem;
            font-size: 0.85rem;
            letter-spacing: 0.3em;
            line-height: 0px;
            
            .tw-nav-link {
                color: #000000;
                text-decoration: none;
                &:hover {
                    color: rgb(40, 120, 122);
                    transition-duration: 0.25s;
                }
            }
            
        }
    }
}

.tw-main-nav  {
    width: 85%;
    // height: 10em;
    margin: 30px  auto;
    padding-left: 0px;
    list-style: none;
    display: flex;
    justify-content: center;

    .tw-main-logo {
        width: 85px;
        height: auto;
        z-index: 5;
    }

    li {
        font-family: 'Helvetica';
        text-transform: uppercase;
        // font-size: 1.2rem;
        font-size: 0.9rem;
        letter-spacing: 0.3em;
        width: auto;
        border-bottom: none;
        margin: auto;
        cursor: pointer;
        transition-duration: 0.25s;

        .tw-nav-link {
            color: #ffffff;
            text-decoration: none;
            &:hover {
                color: rgb(40, 120, 122);
                transition-duration: 0.25s;
            }
        }
        
    }
}